import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Loadable from 'react-loadable';
import { Spin, Icon } from 'antd';
import Layout from './containers/Layout';
import NotFound from './containers/hoc/Notfound';

const antIcon = <Icon type="loading" style={{ fontSize: 34 }} spin />;

const MyLoadingComponent = (props) => {
    if (props.isLoading) {
        return <Spin style={{
            display: 'block',
            margin: '15% auto auto'
        }} indicator={antIcon}
         />
    }
    else if (props.error) {
        return <div style={{
            textAlign: 'center',
            marginTop: '15%'
        }}>Sorry, there was a problem loading the page.</div>;
    }
    else {
        return null;
    }
};

const home = Loadable({
    loader: () => import('./containers/Home'),
    loading: MyLoadingComponent
})

const catalog = Loadable({
    loader: () => import('./containers/Shop'),
    loading: MyLoadingComponent
})

const product = Loadable({
    loader: () => import('./containers/Shop/product'),
    loading: MyLoadingComponent
})

export const Routes = () => (
    <Router>
        <Layout>
            <Switch>
                <Route path="/" exact component={ home } />
                <Route path="/shop"  component={ catalog } />
                <Route path="/shop/:category" exact component={ catalog } />
                <Route path="/shop/:category/:product" exact component={ product } />
                <Route component={ NotFound } />
            </Switch>
        </Layout>
    </Router>
)

    



