import React, { Component, Fragment } from 'react'
import { Link } from 'react-router-dom';
import { Layout, Menu, Icon, Badge, Input } from 'antd';
const { Header } = Layout;
const Search = Input.Search;



export default class MenuCompnent extends Component {
    state = {
        count: 0
      }   
      render() {
          return (
              <Fragment>
                  <Header style={
                      { background: '#fff',
                      height: 53,
                      width: '100%',
                      borderBottom: '1px solid #e8e8e8',
                      }
                      }>
                        <Menu
                            theme="light"
                            mode="horizontal"
                            selectable={false}
                            style={{ lineHeight: '51px', minWidth: 1045, maxWidth: 1065,
                            borderBottom: 'none',
                            margin: 'auto'}}
                        >
                            <Menu.Item key="Home">
                            <Link to="/"><Icon type="home" />Home</Link>
                            </Menu.Item>
                            <Menu.Item key="Shop">
                            <Link to="/shop">
                            <Icon type="shop" />Shop
                            </Link>
                            </Menu.Item>
                            <Menu.Item key="Contact">
                            <Icon type="contacts" />Contact</Menu.Item>
                            <Menu.Item key="search"
                            style={{ borderBottom: '0px', cursor: 'default', outline: 'none'}}>
                            <Search
                            className="search"
                            enterButton
                            placeholder="input search text"
                            onSearch={value => console.log(value)}
                            style={{ width: 350 }}
                            />
                            </Menu.Item>
                            <Menu.Item
                            style={{float: 'right'}}
                            key="signup">
                            <Icon type="solution" />
                            Sign Up</Menu.Item>

                            <Menu.Item style={{float: 'right'}} key="signin">
                            <Icon type="login" />Sign In</Menu.Item>
                            <Menu.Item key="cart" style={{float: 'right'}}>
                            <Badge
                            count={this.state.count}>
                            <Icon style={{
                                fontSize: '18px'
                            }} type="shopping-cart" />
                            </Badge>
                            </Menu.Item>
                        </Menu>
                    </Header>
              </Fragment>
          )
      }
}


